.header {
    display: flex;
    position: fixed;
    top: 0px;
    width: 100%;
    height: 60px;
    justify-content: flex-end;
    align-items: center;
    background-color: green;
    justify-content: space-between;

    h3{
        margin-left: 20px;
    }

    .navbar {
        display: flex;
        gap: 30px;
        margin-right: 20px;
    
        a {
            margin: 0;
            cursor: pointer;
            font-size: 16px;
            color: black;
            
            &:hover {
                color: #007bff;
            }
        }
    }
}

.main{
    display: flex;
    flex-direction: column;
}

.accueil_section{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
    align-items: center;
    background-color: brown;
    min-height: 100vh;

    img{
        max-width: 250px;
        max-height: 250px;
        object-fit: cover;
        border-radius: 180px;
    }

    .spin_animation{
        animation: spin 1s forwards, spin2 1s forwards;
    }

    @keyframes spin {
        from {
          transform: rotateY(180deg);
          width: 0;
          height: 0;
        }
        to {
          transform: rotateY(0deg);
          width: 250px;
          height: 250px;
        }
    }

    @keyframes spin2 {
        from {
          width: 0;
          height: 0;
        }
        to {
          width: 250px;
          height: 250px;
        }
    }

    .typewriter {
        overflow: hidden; /* Ensures the content is hidden */
        border-right: 0.15em solid orange; /* Typewriter cursor */
        white-space: nowrap; /* Keeps the content on a single line */
        animation: typing 4s steps(40, end) 0.75s forwards, blink-caret 0.75s step-end infinite;
        visibility: hidden; /* Initially hide the text */
      }
      
      /* Typing animation */
      @keyframes typing {
        from {
          width: 0;
        }
        to {
          width: 100%;
          visibility: visible;
        }
      }
      
      /* Cursor blink animation */
      @keyframes blink-caret {
        from, to {
          border-color: transparent;
        }
        50% {
          border-color: orange;
        }
      }
}

.projects_section{
    display: flex;
    min-height: 100vh;
    background-color: aqua;
}

.contact_section{
    display: flex;
    min-height: 100vh;
    background-color: burlywood;
}

.footer{
    background-color: #007bff;
    height: 60px;
    width: 100%;

    p{
        margin: 0;
    }
}
  
